.home{
   
    background-color: rgb(241, 241, 241);
   background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("https://images.unsplash.com/photo-1517430816045-df4b7de11d1d?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1051&q=80");
   background-repeat: no-repeat;
   background-size: cover;
   align-items:flex-start;
   justify-content: center;
   display: flex;
   height: 100vh;
   width:100%;
   margin-left: auto;
   margin-right: auto;
   
   font-size: 2rem;
   font-weight: 400;

   color: rgb(241, 241, 241);
   line-height: 1.3;
  
}
.header{
    margin-top: 15%;

    font-size: 2rem;
    font-family: Roboto, sans-serif;
    line-height: 1.5;
    display: flex;
    text-align:start;
    color: rgb(241, 241, 241);
   
    display: inline-block;
    
}
.button{
    color: rgb(241, 241, 241);
    text-decoration: none;
    border: rgb(241, 241, 241) solid 2px;
    padding: 1%;
    
}
.button:hover{
    background-color: #1a83ff;

 
    
}

.textbox{
    width: 30%;
    margin-top: 15%;
    margin-left: 20%;
}



.section-two{
    background-color: #0f0f0f;
    color: rgb(241, 241, 241);
    display: block;
    height: 100vh;
    text-align: center;
    padding-top: 5%;
    font-size: 2rem;
}


@media(min-width: 200px) and (max-width: 560px){

  .navbar{
      position:static;
  }

  .home{
    
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
    /* padding-left: 10%; */
    /* display: inline-block; */
    display: block;
    text-align: center;
   
  }
  .header{
      line-height: 1.3;
      text-align: center;
  }
  .textbox{
      width: 75%;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10%;
  }
  .button{
      display: none;
  }
}