.navbar{
    background-color: #040616;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    right:0%;
    width: 100%;
    
    
}

.menu-bars{
    margin-right: 2rem;
    font-size: 2rem;
    background: none;

}

.nav-menu{
    background-color: #040616;
    /* background-color: #060b26; */
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top:0;
    right: -100%;
    transition: 850ms;
    z-index: 9;
}

.nav-menu.active{
    justify-content: flex-end;
    right:0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;

}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;

}
.nav-text a:hover{
    background-color: #1a83ff;
}

.nav-menu-items{
    width:100%;

}

.navbar-toggle{
    background-color: #040616;
    width:100%;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-left: 1.5rem;
}

span{
    margin-left: 16px;
}